body {
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  font-optical-sizing: auto;
  font-style: normal;
  line-height: 1.4;
  font-size: 1.375rem;
}
@media (max-width: calc(768px - 1px)) {
  body {
    font-size: 1.125rem;
  }
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-weight: 700;
  line-height: 1.2;
  font-family: "Reem Kufi", sans-serif;
  font-optical-sizing: auto;
  text-transform: uppercase;
}

h1 {
  font-size: 4.375rem;
}
@media (max-width: calc(1024px - 1px)) {
  h1 {
    font-size: 3rem;
  }
}
@media (max-width: calc(768px - 1px)) {
  h1 {
    font-size: 2rem;
  }
}

h2,
.h2 {
  font-size: 2.75rem;
}
@media (max-width: calc(1024px - 1px)) {
  h2,
  .h2 {
    font-size: 2rem;
  }
}
@media (max-width: calc(768px - 1px)) {
  h2,
  .h2 {
    font-size: 1.5rem;
  }
}

.w-med {
  max-width: 830px;
  margin: 0 auto;
}

.button {
  background-color: #4992cf;
  color: #ffffff;
  display: inline-block;
  cursor: pointer;
  font-weight: 600;
  padding: 0.85em 2em;
  border-radius: 100px;
  transition: background 0.3s ease;
}
.button:hover, .button:focus {
  background-color: #c92875;
}

.blue {
  color: #4992cf;
}

@media (min-width: 1280px) {
  .container {
    max-width: 1312px !important;
  }
}

.footer__inner {
  display: flex;
  gap: 4rem;
  justify-content: space-between;
  padding: 5rem 0;
}
@media (max-width: calc(1024px - 1px)) {
  .footer__inner {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 3rem 0;
    gap: 3rem;
  }
}
.footer .footer__contact {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (max-width: calc(1024px - 1px)) {
  .footer .footer__contact {
    gap: 1rem;
  }
}
.footer .footer__contact span {
  display: flex;
  align-items: center;
}
.footer .footer__contact span i {
  margin-right: 1rem;
  flex-shrink: 0;
}
.footer .footer__contact span svg {
  width: 30px;
  height: auto;
}
.footer .footer__contact .email {
  word-break: break-all;
  text-align: left;
}
.footer__logo svg {
  width: 300px;
  height: auto;
  max-width: 100%;
}
.footer .footer__social {
  display: block;
  flex-grow: 0;
  align-self: flex-start;
}
.footer .footer__social svg path {
  transition: fill 0.3s ease;
}
.footer .footer__social:hover svg path, .footer .footer__social:focus svg path {
  fill: #4992cf;
}

@media (max-width: calc(1536px - 1px)) {
  .contact-form {
    max-width: 900px;
    margin: 0 auto;
  }
}
.contact-form br {
  display: none;
}
.contact-form p {
  margin: 0;
}
.contact-form .form-grid {
  row-gap: 3rem;
  column-gap: 6rem;
}
@media (max-width: calc(1536px - 1px)) {
  .contact-form .form-grid {
    column-gap: 3rem;
  }
}
@media (max-width: calc(768px - 1px)) {
