.gradient {
  height: 36px;
  background: linear-gradient(90deg, rgb(71, 146, 207) 0%, rgb(56, 159, 84) 25%, rgb(229, 137, 49) 50%, rgb(201, 39, 118) 75%, rgb(120, 43, 120) 100%);
}
@media (max-width: calc(768px - 1px)) {
  .gradient {
    height: 24px;
  }
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  right: 0;
  z-index: 99;
}

.icon {
  text-align: center;
}
.icon svg {
  max-width: 100%;
  height: auto;
}
.icon p {
  font-weight: 700;
  font-size: 1.25rem;
  margin: 0 auto;
  max-width: 150px;
  line-height: 1.22;
}
@media (max-width: calc(1024px - 1px)) {
  .icon p {
    font-size: 1rem;
  }
}

.logo {
  max-width: 60%;
  width: 340px;
  display: block;
  margin-top: 3rem;
}
@media (max-width: calc(768px - 1px)) {
  .logo {
    margin-top: 1rem;
  }
}
.logo img {
  width: 100%;
  height: auto;
}

.section {
  padding: 7rem 0;
}
.section--pink {
  background-color: #c92875;
  color: #ffffff;
}
@media (min-width: 1280px) {
  .section--pink {
    background-image: url("../../static/shape-white.svg");
    background-repeat: no-repeat;
    background-position: calc(100% - 3rem) center;
  }
}
.section--grey {
  background-color: #efefef;
  position: relative;
}
@media (min-width: 1024px) {
  .section--grey {
    background-image: url("../../static/shape-mix.svg"), url("../../static/shape-blue.svg");
    background-repeat: no-repeat, no-repeat;
    background-position: 3rem -3rem, calc(100% - 3rem) calc(100% - 2rem);
  }
}
.section--grey .container {
  position: relative;
  z-index: 2;
}
.section--grey .wheel {
  position: absolute;
  bottom: 2rem;
  left: 0;
  transform: translateX(-50%);
  z-index: 1;
  width: 280px;
  height: 280px;
}
@media (max-width: calc(1536px - 1px)) {
  .section--grey .wheel {
    margin-left: -3rem;
  }
}
@media (max-width: calc(1024px - 1px)) {
  .section--grey .wheel {
    display: none;
  }
}
@media (max-width: calc(768px - 1px)) {
  .section {
    padding: 4rem 0;
  }
}

.home-video {
  overflow: hidden;
}

.video-background {
  position: relative;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  background-color: #343434;
}
@media (max-width: 1300px) {
  .video-background {
    /* height = 100 * (9 / 16) = 56.25 */
    height: 60vw;
  }
}
.video-background video {
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  width: 100vw;
  height: 100vh;
  max-width: none;
  transform: translate(-50%, -50%);
}
@media (min-aspect-ratio: 16/9) {
  .video-background video {
    /* height = 100 * (9 / 16) = 56.25 */
    height: 56.25vw;
  }
}
@media (max-aspect-ratio: 16/9) {
  .video-background video {
    /* width = 100 / (9 / 16) = 177.777777 */
    width: 177.78vh;
  }
}
@media (max-width: 1300px) {
  .video-background video {
    /* height = 100 * (9 / 16) = 56.25 */
    width: 110vw;
  }
}